// /--------- mobile menu-------/ 
.menu__header{
    display: flex;
    justify-content: center;
    align-content: center;
    padding: 0px;
    margin-left: auto;
    margin-top: 25px;
    background-color: transparent;
    cursor: pointer;
    border: none;
    outline: none;
  
    @media (min-width: 768px) {
    margin-left: 484px;
    margin-top: 52px;
    z-index: 999;
    }
    @media (min-width: 1280px) {
      display: none;
      }
    }
  
  .menu__container {
      position: fixed;
      top: 0px;
      right:  0px;
      width: 250px;
      height: 100vh;
      background-color: $mobile-menu-background;
      z-index: 999;
      transform: translateX(100%);
      transition: transform 250ms ease-in-out;
      filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));
      @media (min-width: 1280px) {
        width: 254px;
        }
      &.is-open {
      transform: translateX(0%);
    }
  }
  
  .mobile__button-close{
    width: 14;
    height: 14px;
    display: flex;
    justify-content: center;
    align-content: center;
    padding: 0px;
    margin-left: auto;
    margin-right: 28px;
    margin-top: 23px;
    background-color: $mobile-menu-background;
    fill: $button-white;
    cursor: pointer;
    border: none;
    outline: none;
    transform: rotate(-90deg);
  }
  
  .mobile__section{
  padding-top: 23px;
  padding-right: 20px;
  padding-left: 21px;
  display: flex;
  flex-direction: column;
  }
  
  .mobile__button-buy-now{
    width: 209px;
    height: 44px;
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.31;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    border: none;
    border-radius: 22px;
    background-color: $button-white;
    transition: 0.5s;
    color: $social-link-hover;
    @media (min-width: 768px) {
      width: 186px;
      }
  
    &:hover {
      background-color: $button-hover-grey;
      transition: 0.5s;
  }
  }
  .mobile__button-icon{
    margin-left: 16px;
    display: flex;
    justify-content: center;
   
  }
  .mobile__navigate-list{   
    padding-top: 32px;
  }
  .mobile__navigate-item{
    padding-top: 11px;
    height: 42px;
    padding-left: 21px;
    border-top: 1px solid #E18298;
    &:last-child{
    border-bottom: 1px solid #E18298;
    }
  }
  
  .mobile__navigate-link{
    
    font-weight: 700;
    font-size: 14px;
    line-height: 1.29;
    color: $text-color-white;
    &:hover{
      color: $text-color-red;
    }
  }
  
  
  
  
  
  
  
  
  